.splash-container {
    position: relative;
    width: 100%;
    height: 50vw;
}

.splash-text-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 10px;
    background-color: #080808cf;
    backdrop-filter: blur(4px);
    border-radius: 10px;
    box-shadow: 5px 5px 15px #010101aa;
}

.splash-text-container h1 {
    margin: 10px;
    font-size: 5vw;
    color:#fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
}

.splash-text-container h2 {
    margin: 10px;
    font-size: 2.5vw;
    color:#fff;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-style: italic;
}